.loader-container{
  position: fixed;
  top: 45%;
  left: 45%;
  z-index: 100;
}
.lds-default {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-default div {
    position: absolute;
    width: 6px;
    height: 6px;
    background:white;
    border-radius: 50%;
    -webkit-animation: lds-default 1.2s linear infinite;
            animation: lds-default 1.2s linear infinite;
  }
  .lds-default div:nth-child(1) {
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    top: 37px;
    left: 66px;
  }
  .lds-default div:nth-child(2) {
    -webkit-animation-delay: -0.1s;
            animation-delay: -0.1s;
    top: 22px;
    left: 62px;
  }
  .lds-default div:nth-child(3) {
    -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s;
    top: 11px;
    left: 52px;
  }
  .lds-default div:nth-child(4) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
    top: 7px;
    left: 37px;
  }
  .lds-default div:nth-child(5) {
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
    top: 11px;
    left: 22px;
  }
  .lds-default div:nth-child(6) {
    -webkit-animation-delay: -0.5s;
            animation-delay: -0.5s;
    top: 22px;
    left: 11px;
  }
  .lds-default div:nth-child(7) {
    -webkit-animation-delay: -0.6s;
            animation-delay: -0.6s;
    top: 37px;
    left: 7px;
  }
  .lds-default div:nth-child(8) {
    -webkit-animation-delay: -0.7s;
            animation-delay: -0.7s;
    top: 52px;
    left: 11px;
  }
  .lds-default div:nth-child(9) {
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
    top: 62px;
    left: 22px;
  }
  .lds-default div:nth-child(10) {
    -webkit-animation-delay: -0.9s;
            animation-delay: -0.9s;
    top: 66px;
    left: 37px;
  }
  .lds-default div:nth-child(11) {
    -webkit-animation-delay: -1s;
            animation-delay: -1s;
    top: 62px;
    left: 52px;
  }
  .lds-default div:nth-child(12) {
    -webkit-animation-delay: -1.1s;
            animation-delay: -1.1s;
    top: 52px;
    left: 62px;
  }
  @-webkit-keyframes lds-default {
    0%, 20%, 80%, 100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    50% {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }
  }
  @keyframes lds-default {
    0%, 20%, 80%, 100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    50% {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }
  }
  
.modal {
    position: fixed;
    left:0;
    top:0;
    z-index: 999;
    width: 100%;
    min-height: 100vh;
    background-color: rgba(0,0,0, 0.7);
    display: none;
    align-items: center;
    justify-content: center;
}
.modal.is-open{
    display: flex;
}
.modal-container{
    position: relative;
    background-color: #dcca87;;
    min-width: 280px;
    max-width: 350px;
    min-height: 200px;
    max-height: 600px;
    overflow-y: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.493),0 6px 20px 0 rgba(0, 0, 0, 0.527);
    -webkit-animation-name: animatetop;
            animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 10px 30px;
}
.modal-close{
    color: black;
    position: absolute;
    background: none;
    top: 5px;
    right: 10px;
    font-size: 15px;
    border: 0;
    border-radius: 100%;
    padding: 2px 6px;
}
.modal-close:hover,
.modal-close:focus {

  color: black;
  text-decoration: none;
  cursor: pointer;
}
.modal-body {
    color: black;
    font-size:18px;
    text-align: center;
}

.modal-cancel{
    background: none;
    border: 1px solid black;
    font-family: 'Cormorant Upright', serif;
    font-weight: bold;
    font-size: 18px;
    margin: 10px;
    margin-top: 20px;
    padding: 10px;
    width: 80px;
}

.modal-remove, .modal-update{
    background-color: black;
    border: 1px solid black;
    font-family: 'Cormorant Upright', serif;
    color: white;
    font-size: 18px;
    margin: 10px;
    margin-top: 20px;
    padding: 10px;
    width: 80px;
}

.modal-container h3{
    font-family: 'Cormorant Upright', serif;
    color: black;
    font-size: 22px;
    margin-bottom: 15px;
}
